import { writeFile, mkdir } from 'fs/promises';
import path from 'path';
import fs from 'fs';

// Helper: ensure directory exists
async function ensureDir(dirPath: string) {
  if (!fs.existsSync(dirPath)) {
    await mkdir(dirPath, { recursive: true });
  }
}

// Helper: generate file name
function generateFileName(originalName: string, pattern: 'original' | 'slug' | 'slug-unique' | 'custom', customName?: string) {
  const ext = path.extname(originalName);
  const base = path.basename(originalName, ext);

  switch (pattern) {
    case 'original':
      return originalName;
    case 'custom':
      return `${customName}${ext}`;
    default:
      return originalName;
  }
}

interface SaveFileOptions {
  dir: string;
  pattern: 'original' | 'slug' | 'slug-unique' | 'custom';
  customName?: string;
  multiple?: boolean;
}

export async function saveFilesFromFormData(
  formData: FormData,
  fieldName: string,
  options: SaveFileOptions
): Promise<any> {
  const { dir, pattern, customName, multiple = false } = options;

  await ensureDir(dir);
  const result: any = multiple ? [] : null;

  const files = formData.getAll(fieldName).filter((item): item is File => item instanceof File && item.name);

  for (let index = 0; index < files.length; index++) {
    const file = files[index];
    const nameToUse =
      pattern === 'custom' && multiple
        ? `${customName}-${index + 1}`
        : pattern === 'custom'
        ? customName!
        : file.name;

    const finalFileName = generateFileName(nameToUse, pattern, nameToUse);
    const bytes = await file.arrayBuffer();
    const buffer = Buffer.from(bytes);
    const fullPath = path.join(dir, finalFileName);

    await writeFile(fullPath, buffer);
    const fileUrl = fullPath.split('public')[1].replace(/\\/g, '/'); // for URL use

    const info = {
      originalName: file.name,
      savedAs: finalFileName,
      size: file.size,
      type: file.type,
      url: `/uploads${fileUrl}`,
    };

    if (multiple) {
      result.push(info);
    } else {
      return info;
    }
  }

  return result;
}
